body * {
    font-family: 'Poppins', sans-serif;
}
.hero-banner__button {
    display: inline-block;
    padding: 15px 30px;
    background-color: #f75227;
    color: #fff;
    font-size: 16px;
    font-weight: 700;
    text-transform: uppercase;
    border: none;
    cursor: pointer;
    transition: all 0.3s;
    border-radius: 50px;
    &:hover {
        background-color: transparent;
        color: #fff;
        border: 2px solid #f75227;
    }
    a { color: inherit; }
    a:hover { color: inherit; }

    &.button_white {
        background-color: #fff;
        color: #2ea3f2;
        &:hover {
            background-color: transparent;
            color: #fff;
        }
    }
}
.bakery_antian__hero-banner {
    padding: 200px 40px;
    position: relative;
    overflow: hidden;
    background-color: #0b3354;
    background-size: cover;
    background-repeat: no-repeat;
    
    .hero-banner-content {
        display: flex;
        flex-direction: column;
        gap: 40px;
        z-index: 3;
        .hero-banner__title {
            font-weight: 700;
            color: #fff;
            margin: 0;
            font-size: 40px;
            line-height: 50px;
            width: 70%;
            text-transform: uppercase;
            mark {
                background-color: rgba(0, 0, 0, 0);
                color: #f75227;
            }
        }
        .hero-banner__desc {
            color: #fff;
            font-size: 20px;
            line-height: 30px;
            font-weight: 400;
            margin: 0;
            width: 62%;
        }
    }
    .hero-banner-buttons {
        display: flex;
        gap: 20px;
    }
}

.bakery_antian__image-banner {
    padding: 60px 0;
    .container {
        width: 100%;
        max-width: 1200px;
        margin-right: auto;
        margin-left: auto;
        padding: 0 15px;
    }
    .image-banner__inner {
        display: flex;
        align-items: center;
        gap: 5.5%;
        .image-banner-content {
            width: 55%;
            .image-banner__subtitle {
                font-size: 18px;
                text-transform: uppercase;
                color: #0c71c3;
                line-height: 1.9em;
                margin: 0 0 10px;
                font-weight: 700;
            }
            .image-banner__title {
                margin: 0 0 30px;
                color: #000;
                font-weight: 700;
                font-size: 35px;
                line-height: 1.2em;
            }
            .image-banner__desc {
                font-size: 18px;
                font-weight: 400;
            }
        }
        .image-banner-image {
            width: 45%;
            img { 
                display: block;
                margin: 0 auto;
                width: 50%;
            }
            &.medium {
                img {
                    width: 75%;
                }
            }
            &.large {
                img {
                    width: 100%;
                }
            }
        }
    }
}

/* Slick Slider Arrows */
ul.slick-dots {
    flex-wrap: wrap;
}

.slick-arrow {
    width: 40px !important;
    height: 40px !important;
    font-size: 0 !important;
    background: rgba(0,0,0,0) !important;
    cursor: pointer !important;
    top: 40%;
    transform: translateY(-50%);
    border: none !important;
    position: absolute;
    display: block;
}

.slick-arrow:before {
    width: 16px;
    height: 16px;
    content: "";
    display: block;
    position: absolute;
    top: 11px;
}

.slick-prev {
    left: -40px;
    z-index: 1;
}

.slick-prev:before {
    border-top: 3px solid #000000;
    border-left: 3px solid #000000;
    transform: rotate(-45deg);
}

.slick-prev[aria-disabled=true] {
    opacity: .4;
    pointer-events: none;
}

.slick-next {
    right: -30px;
}

.slick-next:before {
    border-top: 3px solid #000000;
    border-left: 3px solid #000000;
    transform: rotate(135deg);
}

.slick-next[aria-disabled=true] {
    opacity: .4;
    pointer-events: none;
}

/* Slick Slider Dots */
.slick-dots {
    text-align: center; /* Center align dots */
    margin-top: 20px; /* Space above dots */
}

.slick-dots li {
    display: inline-block;
    margin: 0 5px; /* Space between dots */
}

.slick-dots li button {
    font-size: 0; /* Hide default button text */
    background-color: #dce1fc; /* Dot color */
    width: 12px; /* Dot width */
    height: 12px; /* Dot height */
    border-radius: 50%; /* Rounded dots */
    border-color: #506cf0; /* Remove dot border */
    padding: 5px;
}

.slick-dots li.slick-active button {
    background-color: #506cf0; /* Active dot color */
}

.slick-dots li button:hover {
    background-color: #506cf0; /* Dot color on hover */
}

.btn-main {
    font-size: 16px;
    min-width: 100px;
    text-align: center;
    color: #fff;
    display: inline-block;
    font-weight: 600;
    border: 2px solid rgba(0, 0, 0, 0);
    background-color: #f75227;
    cursor: pointer;
    letter-spacing: .16px;
    word-break: break-word;
    text-decoration: none;
    position: relative;
    padding: 8px 20px;
    margin: 0;
    line-height: 1.4;
    border-radius: 50px;
    transition: 150ms ease-out;
    a {
        color: inherit;
        text-decoration: none;
    }
}

