.bakery_antian__toggle-image-box {
    margin: 100px 0 50px;
    .bakery_antian__heading {
        text-align: center;
        h2 {
            font-weight: 600;
            line-height: 1.3;
            color: #000;
            margin-bottom: 20px;
            font-size: 40px;
        }
    }
    .bakery_antian__toggle-box-image {
        display: flex;
        gap: 40px;
        .bakery_antian__toggle-box-image-item {
            width: calc(32% - 10px);
            position: relative;
            display: flex;
            flex-wrap: wrap;
            flex-direction: column;
            text-align: center;
            .bakery_antiab__toggle-box-image-content {
                background: #f75227;
                width: 90%;
                position: relative;
                z-index: 11;
                margin: -50px auto 0;
                font-size: 16px;
                .bakery_antian__toggle-box {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    padding: 20px 36px 28px 36px;
                    box-shadow: 0 .375rem .875rem 0 rgba(0, 0, 0, .13), 0 .0625rem .25rem 0 rgba(0, 0, 0, .11);
                    cursor: pointer;
                    gap: 5px;
                    i {
                        font-size: 18px;
                        color: #fff;
                        font-weight: 600;
                    }
                    h4 {
                        font-size: 18px;
                        color: #fff;
                        margin: 0;
                        padding-left: 10px;
                        font-weight: 600;
                    }
                }
                .bakery_antian__toggle-box-content {
                    margin: 0 0 15px;
                    font-size: 16px;
                    line-height: 1.4em;
                    font-weight: 400;
                    color: #fff;
                    text-align: left;
                    padding: 24px;
                    display: none;
                }
            }
        }
    }
}

.bakery_antian__logo_slider {
    margin: 50px 0;
    .bakery_antian__logo_slider--heading {
        display: flex;
        justify-content: space-between;
        margin: 0 0 40px;
        h2 {
            font-size: 24px;
            color: #0c71c3;
            margin: 0;
            font-weight: 500;
        }
        a {
            color: #000;
            font-size: 14px;
            font-weight: 700;
            text-transform: uppercase;
            padding-right: 2em;
            margin: 0;
            &::after {
                content: "\f061";
                font-family: "Font Awesome 6 Pro";
                margin-left: 5px;
                font-weight: 400;
                vertical-align: middle;
            }
        }
    }
    .bakery_antian__slider {
        .slick-list .slick-track {
            display: flex;
            gap: 30px;
        }
        &.image-border-enable {
            .slick-list .slick-track { gap: 20px; }
            .bakery_antian__slider-image {
                border: 1px solid lightgray;
                padding: 5px;
            }
        }
    }
    .bakery_antian__slider-image {
        padding: 0 20px 20px;
    }
    .bakery_antiab__slider-title h6 {
        color: #000;
        font-size: 13px;
        line-height: 1.3;
        text-transform: uppercase;
        font-weight: 700;
        margin: 0;
    }
}

