.bakery_antian__customer_stories {
    .container {
        max-width: 1200px;
        margin: 0 auto;
    }
    .bakery_antiann__customer_stories__inner {
        padding-top: 100px;
        background-size: cover;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        overflow: hidden;
        align-items: center;
        .bakery_antian__heading {
            width: 40%;
            position: relative;
            z-index: 1;
            h2 {
                font: 500 50px / 1 "Poppins", sans-serif;
                color: #000;
                line-height: 60px;
                margin-bottom: 30px;
            }
            p {
                font: 500 20px / 1 "Poppins", sans-serif;
                line-height: 30px;
                margin-bottom: 30px;
            }
        }
        .bakery_antian__customer_stories_slider {
            width: 55%;
            padding: 40px;
            .bakery_antian__slider-item {
                background-color: #fff;
                box-shadow: rgba(0, 0, 0, .1) 0 20px 30px 0px;
                padding: 40px 60px;
                .bakery_antian__customer_stories_review__content {
                    font: 500 22px / 1 "Poppins", sans-serif;
                    line-height: 34px;
                    color: #1870b8;
                    margin: 0 0 40px;
                }
                .bakery_antiann__customer_stories_review__author {
                    display: flex;
                    gap: 25px;
                    align-items: center;
                    .bakery_antiann__customer_stories_review__author__image {
                        width: 100px;
                        height: 100px;
                    }
                    .bakery_antiann__customer_stories_review__author__name {
                        h4 {
                            font: 600 25px / 1 "Poppins", sans-serif;
                            line-height: 32px;
                            color: #000;
                            margin: 0 0 10px;
                        }
                        p {
                            font: 500 13px / 1 "Poppins", sans-serif;
                            line-height: 28px;
                            color: #1870b8;
                        }
                    }
                }
                
            }
        }
    }
    .slick-arrows {
        display: flex;
        justify-content: center;
        width: 50%;
        .slick-prev {
            margin-right: 10px;
            background: url(../images/arrow-left.svg) no-repeat;
            background-size: 100%;
            width: 100px;
            height: 18px;
            background-repeat: no-repeat;
            cursor: pointer;
            transition: all .3s linear;
            border: none;
        }
        .slick-next {
            margin-left: 10px;
            background: url(../images/arrow-right.svg) no-repeat;
            background-size: 100%;
            width: 100px;
            height: 18px;
            background-repeat: no-repeat;
            cursor: pointer;
            border: none;
            transition: all .3s linear;
        }
    }
    .bakery_antiann__customer_stories__bottom {
        display: flex;
        width: 100%;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        padding: 30px 0 50px;
        .bakery_antiann__customer_stories__view_more {
            line-height: 50px;
            text-transform: uppercase;
            letter-spacing: 3px;
            border-bottom: 1px solid #000;
            a {
                text-decoration: none;
                color: #000;
            }
        }
    }
    margin: 50px 0;
}

